import React, { useState, useEffect } from 'react';

const LightMobile = () => {
  //    X  state , Y  
  const [point2X, setPoint2X] = useState(0);
  const [point5Y, setPoint5Y] = useState(300);

  //  Y 
  const point2Y = 0;
  const point5X = 0;
  
  //     
  const point1X = 0;
  const point1Y = 0;
  
  const point3X = 328;
  const point3Y = 489;
  
  const point4X = 321;
  const point4Y = 489;

  //    
  const duration = 150; //   
  let moveRange = 20; //  

  useEffect(() => {
    const interval = setInterval(() => {
      setPoint2X((prev) => {
        let newValue = prev + moveRange;
        if (newValue > 280 || newValue < 0) moveRange = -moveRange;
        return newValue;
      });

      setPoint5Y((prev) => {
        let newValue = prev + moveRange;
        if (newValue > 800 || newValue < 400) moveRange = -moveRange;
        return newValue;
      });
    }, duration);

    return () => clearInterval(interval); //     
  }, [duration, moveRange]);

  // path  state  
  const pathData = `M${point1X} ${point1Y} L${point2X} ${point2Y} L${point3X} ${point3Y} L${point4X} ${point4Y} L${point5X} ${point5Y} Z`;

  return (
    <div className="mShow">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 360 850"
        style={{
          position: "absolute",
          top: 0,
          left: "-40px",
          zIndex: -10,
          width: "360px",
          height: "850px",
        }}
        fill="none"
      >
        <path d={pathData} fill="#1f1f1f"/>
      </svg>
    </div>
  );
};

export default LightMobile;
